/******************************************************************
VARIABLES - COLORS
******************************************************************/

// Theme colors
$color01:                   #263938;
$color01-light:             #3c4d4c;
$color02:                   #e5f0e4;
$color02-light:             #eff6ef;
$color02-lighter:           #f7fdf7;
$color02-lightest:          #eff6ef;
$color03:                   #b02937;
$color03-light:             #b83f4b;


// Gradients
$gradient01:                linear-gradient(0deg, $color01 0%, $color02 100%);


// Grayscale
$color-black:               #1c1c1c;
$color-white:               #ffffff;

$color-gray-lightest:       #f6f6f6;
$color-gray-light:          #cecece;
$color-gray:                #9b9b9b;
$color-gray-dark:           #6c6c6c;
$color-gray-darkest:        #3c3c3c;


// State & alert colors
$color-positive:            #9adc94;
$color-negative:            #b02937;
$color-warning:             #e9bf35;
$color-warning-dark:        #3671a0;
$color-neutral:             $color-gray;
$color-disabled:            $color-gray-light;
$color-placeholder:         $color-gray;


// Opacities
$opacity-sm:                0.72;
$opacity-md:                0.48;
$opacity-lg:                0.24;


// Base element colors
$color-text:                $color01;
$color-heading:             $color01;
$color-link:                $color01;
$color-hover:               rgba($color-link, $opacity-md);
$color-visited:             rgba($color-link, $opacity-md);

$color-selection:           $color-white;
$color-selection-bg:        $color01;

$color-border:              $color-gray-lightest;
$color-border02:            $color02;
$color-border-dark:         $color-gray-light;