/******************************************************************
WORDPRESS LOGIN PAGE

This stylesheet is loaded on the WordPress login page

For more info, check out the codex:
http://codex.wordpress.org/Creating_Admin_Themes
******************************************************************/

// Colors
@import "1_config/colors";


body {
	background: $color-white;
}

.login h1 a {
	background: url(../../assets/images/login-logo.png) no-repeat top center; 
	width: 320px;
	height: 120px;
	text-indent: -9999px;
	overflow: hidden;
	padding-bottom: 15px;
	display: block;
}

form {
	margin-left: 8px;
	padding: 26px 24px 46px;
	font-weight: normal;
	background: white;
	border: 1px solid #E5E5E5;
	border-radius: 3px;
	box-shadow: none;
}

label {
	font-weight: 700;
}

body form .input {
	font-family: "HelveticaNeue-Light", "Helvetica Neue Light" ,"Helvetica Neue" ,sans-serif;
	font-weight: 200;
	font-size: 24px;
	width: 97%;
	padding: 3px;
	margin-top: 2px;
	margin-right: 6px;
	margin-bottom: 16px;
	border: 1px solid #E5E5E5;
	border-radius: 0;
	background: #FBFBFB;
	outline: none;
}

#loginform {
	border: 0;
	box-shadow: none;
}

#wp-submit {
	background: $color01;
	border: 0;
	border-radius: 0;
}